@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

/* Slider */
.slick-loading .slick-list { background: #fff url("../img/ajax-loader.gif") center center no-repeat; }

/* Icons */
@font-face { font-family: "slick"; src: url("../fonts/slick.eot"); src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg"); font-weight: normal; font-style: normal; }

/* Arrows */
.slick-prev, .slick-next { position: absolute; display: block; height: 20px; width: 20px; line-height: 0px; font-size: 0px; cursor: pointer; background: transparent; color: transparent; top: 50%; margin-top: -10px; padding: 0; border: none; outline: none; }

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { outline: none; background: transparent; color: transparent; }

.slick-prev:hover:before, .slick-prev:focus:before, .slick-next:hover:before, .slick-next:focus:before { opacity: 1; }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before { opacity: 0.25; }

.slick-prev:before, .slick-next:before { font-family: "slick"; font-size: 20px; line-height: 1; color: white; opacity: 0.75; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-prev { left: -25px; }

[dir="rtl"] .slick-prev { left: auto; right: -25px; }

.slick-prev:before { content: "←"; }

[dir="rtl"] .slick-prev:before { content: "→"; }

.slick-next { right: -25px; }

[dir="rtl"] .slick-next { left: -25px; right: auto; }

.slick-next:before { content: "→"; }

[dir="rtl"] .slick-next:before { content: "←"; }

/* Dots */
.slick-dots { position: absolute; bottom: 22px; list-style: none; display: block; text-align: center; padding: 0; width: 100%; }

.slick-dots li { position: relative; display: inline-block; cursor: pointer; }

.slick-dots li button { display: block; padding: 3px; background: none; outline: none; line-height: 0px; font-size: 0px; color: red; cursor: pointer; }

.slick-dots li button:before { content: ""; display: block; height: 10px; width: 10px; box-sizing: border-box; border-radius: 50%; background: #979795; transition: all 0.1s ease; }

.slick-dots li button:hover:before { background: #4E4E4E; }

.slick-dots li.slick-active button:before { background: #4E4E4E; }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

@font-face { font-family: 'NeoSansProRegular'; font-weight: normal; font-style: normal; src: url("../fonts/NeoSansProRegular.eot"); src: url("../fonts/NeoSansProRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/NeoSansProRegular.woff") format("woff"), url("../fonts/NeoSansProRegular.ttf") format("truetype"); }

/* font-family: "NeoSansProBold"; */
@font-face { font-family: 'NeoSansProBold'; font-weight: normal; font-style: normal; src: url("../fonts/NeoSansProBold.eot"); src: url("../fonts/NeoSansProBold.eot?#iefix") format("embedded-opentype"), url("../fonts/NeoSansProBold.woff") format("woff"), url("../fonts/NeoSansProBold.ttf") format("truetype"); }

html { background-color: #fff; -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

body { font-family: NeoSansProRegular; }

.header { background-color: #eee; width: 100%; max-height: 145px; height: 100%; border-top: 5px solid #8c363f; }

.header__container { display: flex; justify-content: space-between; width: 100%; max-width: 1000px; margin: auto; padding: 5px 10px; align-items: center; }

.header__title { min-width: 265px; text-align: center; font-size: 16px; font-weight: bold; color: black; margin: auto; padding: 0 5px; }

.header__title-link { color: black; text-decoration: none; }

.header__title-span { color: #8c363f; font-size: 18px; }

.logo { margin: auto; width: 100%; height: 100%; max-width: 100px; max-height: 100px; align-items: center; }

.logo__image { width: 100%; height: 100%; max-width: 100px; max-height: 100px; }

.logo__link { width: 100%; height: 100%; max-width: 100px; max-height: 100px; border-radius: 100px; overflow: hidden; transition: all 600ms; }

.logo__link:hover { box-shadow: 0 0 10px #8c363f; transition: all 600ms; }

.logo__link-right:hover { box-shadow: 0 0 10px grey; }

.infobar { width: 100%; border-bottom: 1px solid darkgray; }

.infobar__container { max-width: 1000px; margin: auto; display: flex; justify-content: space-around; height: 25px; }

.infobar__item { width: 33%; text-align: center; margin: auto; font-size: 16px; font-weight: 600; }

.infobar__email { text-decoration: none; color: black; }

.infobar__email:hover { text-decoration: underline; color: #8c363f; }

.lang { width: 44px; height: 25px; background: red; }

.lang__to-ua { background: url(../img/lang/ua.svg) no-repeat center; }

.lang__to-en { background: url(../img/lang/uk.svg) no-repeat center; }

.hero { width: 100%; height: 700px; background-color: #eee; }

@media only screen and (max-width: 1000px) { .hero { height: 100%; } }

@media only screen and (max-width: 480px) { .hero { display: none; } }

.hero__container { position: relative; }

.nav { z-index: 999; width: 100%; position: absolute; top: 35px; transition: all 600ms ease; }

.nav__container { transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); max-width: 840px; width: 100%; margin: auto; }

.nav__list { max-width: 840px; width: 100%; border-top: 3px solid #8c363f; margin: auto; display: flex; justify-content: space-between; }

.nav__item { text-align: right; display: flex; width: 16.7%; height: 60px; }

.nav__link { word-wrap: normal; width: 100%; max-width: 140px; min-width: 125px; padding: 5px; height: 60px; background-color: #eee; text-decoration: none; line-height: 76px; color: #000; font-size: 18px; font-weight: 600; transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); }

.nav__link:hover { color: #8c363f; height: 110px; line-height: 175px; transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); }

.nav__current { background-color: #8c363f; color: #eee; height: 110px; line-height: 175px; }

.nav__current:hover { color: #FFF; }

.nav .lang { display: none; }

.nav-fixed { z-index: 999999; width: 100%; position: fixed; top: 0; }

.nav-fixed__container { box-shadow: 0 0 10px rgba(0, 0, 0, 0.6); border-top: 2px solid #8c363f; background: #eee; max-width: 100%; width: 100%; transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); margin: auto; }

.nav-fixed__list { border: 0; max-width: 1000px; width: 100%; margin: auto; display: flex; justify-content: space-between; }

.nav-fixed__item { text-align: right; display: flex; width: 16.666666%; }

.nav-fixed__link { width: 100%; max-width: auto; min-width: 125px; padding: 5px; height: 30px; text-align: center; background-color: #eee; text-decoration: none; line-height: 20px; color: #000; font-size: 18px; font-weight: 600; transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); }

.nav-fixed__link:hover { color: #8c363f; box-shadow: inset 0px 0px 0px 1px #8c363f; transition: all 400ms ease; }

.nav-fixed__current { background-color: #8c363f; color: #eee; height: 30px; line-height: 20px; }

.nav-fixed__current:hover { color: #FFF; }

.nav-fixed .lang { width: 54px; height: 30px; }

.carousel { position: relative; height: 700px; width: 100%; cursor: pointer; }

@media only screen and (max-width: 1000px) { .carousel { height: auto; } }

.carousel__container { margin: 0 auto; max-width: 1920px; width: 100%; }

.carousel__item { z-index: -1; }

.carousel__img { width: 100%; height: 700px; }

@media only screen and (max-width: 1000px) { .carousel__img { height: auto; } }

.arrow { position: absolute; font-family: "Cooper Black"; cursor: pointer; z-index: 1; color: #eee; opacity: 1; font-size: 80px; width: 100px; top: 0; bottom: 0; background: rgba(255, 255, 255, 0); transition: all .5s ease; }

@media only screen and (max-width: 1000px) { .arrow { display: none; } }

.arrow:hover { color: #8c363f; text-shadow: #000 1px 1px 5px; background: rgba(255, 255, 255, 0.3); }

.arrow__prev { left: 0; }

.arrow__next { right: 0; }

.slick-dots { bottom: 120px; }

.slick-dots li button { padding: 5px; }

.slick-dots li button:before { height: 14px; width: 14px; background: #eee; transition: all 0.3s ease; }

.slick-dots li button:hover:before { background: #8c363f; }

.slick-dots li.slick-active button:before { background: #8c363f; }

.content { width: 100%; background: #eee; height: 100%; }

.content__container { box-shadow: 0 0 10px rgba(0, 0, 0, 0.9); position: relative; width: 1000px; min-height: 500px; margin: 0 auto; margin-top: -300px; display: flex; flex-wrap: wrap; background: #eee; z-index: 9; }

@media only screen and (max-width: 1000px) { .content__container { width: 100%; top: 0; margin-top: 0; } }

.content__container-main { box-shadow: 0 0 10px rgba(0, 0, 0, 0.9); position: relative; width: 1000px; margin: 0 auto; margin-top: -100px; display: flex; flex-wrap: wrap; background: #eee; }

@media only screen and (max-width: 1000px) { .content__container-main { width: 100%; } }

@media only screen and (max-width: 480px) { .content__container-main { margin-top: 0; } }

.content__center { width: 100%; padding: 10px; text-align: justify; }

.content__left { width: 50%; padding: 10px; text-align: justify; }

@media only screen and (max-width: 720px) { .content__left { width: 100%; } }

.content__right { width: 50%; padding: 10px; text-align: justify; }

@media only screen and (max-width: 720px) { .content__right { width: 100%; } }

.content__name { font-size: 22px; margin-bottom: 10px; }

.footer { background-color: #232528; width: 100%; border-top: 5px solid #8c363f; }

.footer__container { display: flex; flex-wrap: wrap; width: 1000px; margin: 0 auto; }

@media only screen and (max-width: 1000px) { .footer__container { flex-direction: column-reverse; width: 100%; } }

.footer__copyright { width: 50%; min-width: 300px; padding: 10px; color: #eee; font-size: 16px; }

@media only screen and (max-width: 1000px) { .footer__copyright { width: 100%; text-align: center; } }

.footer__social { width: 50%; padding: 10px; min-width: 200px; margin: 0 auto; text-align: center; }

.footer__social-item { display: inline-block; }

.person { width: 100%; }

.person__list { display: flex; flex-wrap: wrap; }

.person__item { border: 2px solid rgba(140, 54, 63, 0.1); width: 100%; margin-bottom: 10px; }

.person__item:last-child { margin-bottom: 0; }

.person__item:hover { border: 2px solid #8c363f; }

.person__item:hover .person__photo { border: 2px solid #8c363f; }

.person__full-info { clear: both; padding: 0 10px; }

.person__photo { width: 200px; height: 250px; float: left; margin: 10px 10px 10px 10px; border: 2px solid rgba(140, 54, 63, 0.1); overflow: hidden; }

.person__photo:hover .person__name-link { text-decoration: underline; }

.person__info { font-size: 16px; line-height: 16px; font-weight: 500; line-height: 24px; }

.person__info:first-letter { text-transform: capitalize; }

.person__name { font-size: 20px; line-height: 18px; font-weight: 800; line-height: 32px; }

.person__name-link { text-decoration: none; color: #8c363f; font-size: 20px; line-height: 36px; text-transform: capitalize; }

.person__name-link:hover { text-decoration: underline; }

.person__mail { font-size: 16px; line-height: 16px; line-height: 32px; text-decoration: none; color: #8c363f; }

.person__mail-link { text-decoration: none; color: #8c363f; font-size: 18px; line-height: 36px; font-weight: 600; }

.person__mail-link:hover { text-decoration: underline; }

.person__site { font-size: 18px; }

.person__site-link { text-decoration: none; color: #8c363f; font-size: 18px; line-height: 36px; font-weight: 600; }

.person__site-link:hover { text-decoration: underline; }

.person__capt { font-size: 18px; font-weight: 600; margin: 10px 0 5px 20px; text-align: center; }

.event { width: 100%; }

.event__item { opacity: 0; width: 100%; border: 2px solid rgba(140, 54, 63, 0.1); padding: 0 10px 10px 10px; margin-bottom: 10px; transform: translate(0px, 100px); transition: all 300ms linear; }

.event__item:last-child { margin-bottom: 0; }

.event__item:hover { border: 2px solid #8c363f; background: #eee; }

.event__item:hover .event__date { background: #8c363f; border-right: 2px solid #8c363f; border-bottom: 2px solid #8c363f; transition: all 300ms linear; }

.event__item:hover .event__date-day { color: #eee; }

.event__item:hover .event__date-month { color: #eee; }

.event__date { float: left; margin-right: 10px; margin-left: -10px; width: 75px; height: 75px; border-right: 2px solid rgba(140, 54, 63, 0.1); border-bottom: 2px solid rgba(140, 54, 63, 0.1); }

.event__date-day { color: #8c363f; text-align: center; font-size: 30px; font-weight: 600; line-height: 44px; }

.event__date-month { color: #8c363f; text-align: center; font-size: 20px; font-weight: 600; text-transform: uppercase; }

.event__name { font-size: 18px; font-weight: 600; margin-bottom: 10px; }

.event__name-link { color: #8c363f; text-decoration: none; }

.event__name-link:hover { text-decoration: underline; }

.event__description { font-size: 16px; font-weight: 400; line-height: 22px; text-align: justify; }

.event__visible { transition: all 400ms linear; opacity: 1; transform: translate(0px, 0px); }

.map { position: relative; margin-bottom: 10px; }

.map:hover .map__adress { background-color: #8c363f; }

.map__container { position: relative; width: 100%; height: 400px; background-color: #eee; }

.map__adress { position: absolute; width: 100%; bottom: 0; left: 0; padding: 20px; background-color: rgba(140, 54, 63, 0.1); color: white; font-size: 18px; text-align: center; }

.map__adress:hover { background-color: #8c363f; }

.map__zoom-in { background-image: url("../img/map/plus.svg"); }

.map__zoom-out { background-image: url("../img/map/minus.svg"); }

.map__zoom-in, .map__zoom-out { height: 50px; width: 50px; cursor: pointer; margin-left: 10px; margin-top: 10px; background-repeat: no-repeat; background-size: cover; opacity: 0.8; transition: all 0.25s ease; }

.map__zoom-in:hover, .map__zoom-out:hover { opacity: 1; }

.adress { font-size: 18px; padding: 3px; font-weight: 500; }

.adress__link { text-decoration: none; color: #8c363f; font-weight: 600; }

.adress__link:hover { text-decoration: underline; }

.modal-mask { position: fixed; z-index: 9998; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.5); display: table; transition: opacity .3s ease; }

.modal-wrapper { display: table-cell; vertical-align: middle; }

.modal-container { width: 300px; margin: 0px auto; padding: 20px 30px; background-color: #fff; border-radius: 2px; box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33); transition: all .3s ease; font-family: Helvetica, Arial, sans-serif; }

.modal-header h3 { margin-top: 0; color: #42b983; }

.modal-body { margin: 20px 0; }

.modal-default-button { float: right; }

/* The following styles are auto-applied to elements with transition="modal" when their visibility is toggled by Vue.js. You can easily play with the modal transition by editing these styles. */
.modal-enter, .modal-leave-active { opacity: 0; }

.modal-enter .modal-container, .modal-leave-active .modal-container { -webkit-transform: scale(1.1); transform: scale(1.1); }

.breadcrumbs { margin-bottom: 10px; }

.breadcrumbs__list { display: flex; }

.breadcrumbs__item { margin-right: 10px; }

.breadcrumbs__item:after { content: ' < '; font-size: 16px; font-weight: 600; padding: 5px; color: #8c363f; }

.breadcrumbs__item:last-child:after { content: none; }

.breadcrumbs__link { font-size: 16px; font-weight: 600; color: #8c363f; text-decoration: none; }

.breadcrumbs__link:hover { text-decoration: underline; }
